  .flex {
  align-content: stretch;
  align-items: stretch;
  display: flex;
  justify-content: center;

  .flex-content {
    flex: 1 1 0;
    padding: $m $xl;

    &:first-of-type {
      padding-left: 0;
    }

    &:last-of-type {
      padding-right: 0;
    }
  }

  &.flex-wrap {
    flex-wrap: wrap;
  }

  &.space-between {
    justify-content: space-between;
  }
}


@media screen and (max-width: 1119px) {
  .flex {
    display: grid;

    &.space-between {
      justify-content: center;
    }
  }
}
